.container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 55px;
}

.wrapper {
    display: flex;
    align-items: center;
    gap: 10px;
}

.number {
    font-family: sans-serif;
    font-weight: 600;
    font-size: 16px;
    line-height: 125%;
    color: #000;
    padding-right: 10px;
}

.number:first-child {
    margin-right: 10px; 
}

.number:last-child {
    margin-left: 10px; 
}