.greetingForm {
    display: flex;
    flex-direction: column;
}

.inputAndButtonContainer {
    display: flex;
    align-items: center;
    margin-top: 20px;
}

.inputContainer {
    margin-right: 15px;
}

.error {
    margin-top: 10px;
    font-family: sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: #c00;
}

.input {
    padding: 10px;
    border-radius: 5px;
    width: 372px;
    height: 36px;
    font-family: sans-serif;
    font-weight: 500;
    font-size: 16px;
    border: 1px solid #d1d1d1;
}

.input:focus {
    border-color: #06c;
}

.errorInput {
    border: 1px solid #c00;
    border-radius: 5px;
    width: 372px;
    height: 36px;
}

.button {
    margin: 0 0 10px 10px;
    border-radius: 5px;
    border: none;
    padding: 5px 24px;
    width: 78px;
    height: 36px;
    background-color: #06c;
    font-family: sans-serif;
    font-weight: 600;
    font-size: 14px;
    line-height: 143%;
    color: #fff;
    cursor: pointer;
}

.button:focus {
    outline: none;
}

.button:active {
    background-color: #05a;
}

.button:disabled {
    background-color: #06c;
    opacity: 0.5;
    cursor: not-allowed;
}

.text {
    font-family: sans-serif;
    font-weight: 400;
    font-size: 14px;
    color: rgba(0, 0, 0, 0.5);
}

.greeting {
    font-family: sans-serif;
    font-weight: 500;
    font-size: 14px;
    color: #000;
}
